'use client';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { ArrowRight, Church, ShieldCheck, User } from 'lucide-react';
import { useAuth } from '@/hooks/use-auth';
import { useRouter } from 'next/navigation';
import { useEffect } from 'react';
import { Lora } from 'next/font/google';
import { cn } from '@/lib/utils';

const lora = Lora({
  subsets: ['latin'],
  weight: ['400', '700'],
});


export default function LoginPage() {
  const { login, isLoggedIn, isAdmin } = useAuth();
  const router = useRouter();

  useEffect(() => {
    if (isLoggedIn) {
      router.push(isAdmin ? '/admin' : '/my-schedule');
    }
  }, [isLoggedIn, isAdmin, router]);

  const handleLogin = (asAdmin: boolean) => {
    login(asAdmin);
  };

  return (
    <main className="flex min-h-screen flex-col items-center justify-center p-8 bg-background">
      <div className="flex-1 flex flex-col items-center justify-center">
        <div className="text-center mb-12">
          <Church className="h-16 w-16 text-primary mx-auto mb-4" />
          <h1 className={cn("text-5xl font-bold text-foreground tracking-tight sm:text-6xl", lora.className)}>
            Welcome to Weaver's Loom
          </h1>
          <p className="mt-4 text-lg text-foreground/80 max-w-2xl mx-auto">
            The volunteer scheduling module for ParishScribe.
          </p>
        </div>

        <Card className="w-full max-w-md bg-card/80 backdrop-blur-sm border-border/50 shadow-xl">
          <CardHeader>
            <CardTitle>Login</CardTitle>
            <CardDescription>Select your role to continue.</CardDescription>
          </CardHeader>
          <CardContent className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <Button variant="outline" size="lg" onClick={() => handleLogin(true)}>
              <ShieldCheck className="mr-2 h-5 w-5"/>
              Login as Admin
            </Button>
            <Button variant="outline" size="lg" onClick={() => handleLogin(false)}>
              <User className="mr-2 h-5 w-5"/>
              Login as Volunteer
            </Button>
          </CardContent>
        </Card>
      </div>
      <footer className="py-4 text-center text-foreground/60 text-sm">
        <p>A module of ParishScribe</p>
      </footer>
    </main>
  );
}
